import * as THREE from "three";
import { extend } from "@react-three/fiber";

class CirclePatternShaderMaterial extends THREE.ShaderMaterial {
  constructor(texture) {
    super({
      vertexShader: `varying vec2 vUv;
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
      }`,
      fragmentShader: `#include <common>

      uniform vec2 u_resolution;
      uniform vec2 u_mouse;
      uniform float u_time;
      uniform sampler2D iChannel0;
    
      // By Daedelus: https://www.shadertoy.com/user/Daedelus
      // license: Creative Commons Attribution-NonCommercial-ShareAlike 3.0 Unported License.
      #define TIMESCALE 0.25 
      #define TILES 8
      #define COLOR 0.7, 1.6, 2.8

      varying vec2 vUv;
    
      vec3 palette( float t, vec3 a, vec3 b, vec3 c, vec3 d){
        return a + b * cos(6.28318*(c * t + d));
        }
        
        vec3 myPalette(float t){
        vec3 a = vec3(0.5, 0.5, 0.5);
        vec3 b = vec3(0.25,0.25, .52);
        vec3 c = vec3(.5, .5, 1.);
        vec3 d = vec3(0.2, 0.2, 3.885);
        
        return palette(t,a,b,c,d);
        
        }
        
        void mainImage( out vec4 fragColor, in vec2 fragCoord )
        {
        
            vec2 uv = (fragCoord * 2.0 -  u_resolution.xy) / u_resolution.y;
            vec2 uv0 = uv;
            vec3 finalColor = vec3(0.0);
            for(float i=0.0; i< 1.5 ;i++){
            
                uv = fract(uv * 2.5) - 0.5;
        
                float d = length(uv) * exp(-length(uv0));
        
                vec3 col = myPalette(length(uv0) * i*.4 + u_time * .9);
        
                d = sin(d*8.+ u_time) / 8.;
                d = abs(d);
               // d = smoothstep(0.0,1.,d);
                d = pow(0.01 / d,1.2);
        
                finalColor += col * d;
            }
            
            fragColor = vec4(finalColor,1.0);
        }
    
      void main() {
        mainImage(gl_FragColor, vUv * u_resolution.xy);
      }
      `,
      uniforms: {
        iChannel0: { value: texture },
        u_time: { type: "f", value: 0.05 },
        u_speed: { type: "f", value: performance.now() },
        u_mouse: { type: "v2", value: { x: 0, y: 0 } },
        u_resolution: {
          type: "v2",
          value: new THREE.Vector2(window.innerWidth, window.innerWidth),
        },
      },
      transparent: true,
      blending: THREE.NormalBlending,
      depthWrite: false,
    });
  }
}

extend({ CirclePatternShaderMaterial });
