import React, { Suspense } from "react";
import { Loader } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { space } from "../utils/";
import { SimpleShader } from "../shaders/HeartfeltShader";
import useResize from "../hooks/useResize";
import { MouseParallax } from "react-just-parallax";
import "../shaders/CirclePatternShaderMaterial";
import "../shaders/squaresShader";
import AnimatedMesh from "../three/AnimatedMesh";
import { Vector2, DoubleSide } from "three";
import { useMouseMoving } from "../hooks";
import About from "./corporate/about";

export default function CanvasBackground() {
  const { width, height } = useResize();
  const { x, y } = useMouseMoving();
  const time = 0.0008;
  return (
    <div className="moon bg-white fixed flex items-center justify-center w-screen h-screen top-0 left-0 z-20 flex-col">
      <Suspense fallback={<Loader />}>
        {/* <MouseParallax strength={0.01}> */}
          <Canvas performance={{ min: 100,max:10 }} gl flat linear shadows>
          <AnimatedMesh 
                position={[space(0), space(0), -space(250)]}
                    animate={({ meshRef, cloxk, delta }) => {
                      if (meshRef?.current) {
                        meshRef.current.material.uniforms.u_time.value =
                          performance.now() * time;
                        meshRef.current.material.uniforms.u_mouse.value =
                          new Vector2(x, y);
                        meshRef.current.material.uniforms.u_time.needsUpdate = true;
                        meshRef.current.material.uniforms.u_mouse.needsUpdate = true;
                        meshRef.current.uniformsNeedUpdate = true;
                      }
                    }}
                receiveShadow>
                  <planeGeometry args={[width, width]} />
                  <circlePatternShaderMaterial attach={"material"} />
                </AnimatedMesh>
            {/* <Ship position={[space(0),space(0),space(-10)]} /> */}
          </Canvas>
        {/* </MouseParallax> */}
      </Suspense>
    </div>
  );
}
