import React, { useRef } from "react";
import { useFrame } from "@react-three/fiber";
import { useMouseMoving } from "../hooks";
export default function AnimatedMesh({animate, children, ...props }) {
  const meshRef = useRef();

  const {x,y} = useMouseMoving()
  const AnimateFrame = ({ meshRef }) => {
    useFrame(({ clock }, delta) => {
        meshRef?.current &&   animate({ meshRef, clock, delta })
    });
    return null;
};
  return (
    <mesh {...props} ref={meshRef} transparent={true}>
    {(meshRef && animate) && <AnimateFrame meshRef={meshRef} />}
      {children}
    </mesh>
  );
}
