import React from "react";
import jsonData from "../../json/data.json";
const About = ({ ...props }) => {
  return (
    <ul className="text-xl text-black max-w-5xl xl:max-w-3xl Anuphan ">
      {jsonData.personal_info.map((text, index) => (
        <li className="mb-4" key={index}>{text}</li>
      ))}
    </ul>
  );
};

export default About;
